import React from 'react';
import { Button, Form, Input } from 'antd';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';
import logo from '../images/heart-logo.png';

function Register() {
  const navigate = useNavigate();

  const onFinish = async (value) => {
    try {
      const response = await axios.post('/api/user/register', value);
      if (response.data.success) {
        toast.success(response.data.message);
        navigate('/login');
      } else {
        toast.error(response.data.message);
      }
    } catch (error) {
      toast.error('');
    }
  };

  return (
    <div className='auth'>
      <div className='auth-form'>
        <img className='img-login' src={logo} alt='logo' />
        <h4 className='title'></h4>
        <Form onFinish={onFinish} layout='vertical'>
          <Form.Item
            label={<label style={{ color: 'white' }}></label>}
            name='name'
            rules={[
              {
                required: true,
                message: '!',
              },
              { min: 2, message: ' 2 ' },
            ]}
            hasFeedback
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            label={<label style={{ color: 'white' }}>Email</label>}
            name='email'
            rules={[
              {
                required: true,
                message: '!',
              },
              { type: 'email', message: '' },
            ]}
            hasFeedback
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            label={<label style={{ color: 'white' }}></label>}
            name='password'
            rules={[
              {
                required: true,
                message: '!',
              },
              {
                min: 6,
                max: 12,
                message: ' 6-12 ',
              },
            ]}
            hasFeedback
          >
            <Input.Password placeholder='' />
          </Form.Item>

          <Form.Item >
          <Button type='primary' onClick={()=> navigate('/login')} style={{margin: '0 70px 0 0'}}>
              
            </Button>
            <Button type='primary' htmlType='submit'>
              
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
}

export default Register;
