import React from 'react';
import { Button, Col, Form, Input, Row, TimePicker, Upload } from 'antd';
import moment from 'moment';

function ClinicForm({ onFinish, initialValues }) {
  const [form] = Form.useForm();

  return (
    <Form
      layout='vertical'
      form={form}
      onFinish={onFinish}
      initialValues={{
        ...initialValues,
        ...(initialValues && {
          timing: [
            moment(initialValues?.timing[0], 'HH:mm'),
            moment(initialValues?.timing[1], 'HH:mm'),
          ],
        }),
        clinicImage: initialValues?.clinicImage.file,
      }}
    >
      <h4 className='content-title'></h4>
      <Row justify={'center'}>
        <Col sm={18} md={12} lg={10}>
          <Form.Item
            label=''
            name='clinicName'
            rules={[{ required: true, message: '' }]}
            hasFeedback
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            label='  (ex.091-xxx-xxxx)'
            name='clinicTel'
            rules={[
              { required: true, message: '' },
              {
                message: '',
              },
              { pattern: '[0-9]{3}-[0-9]{3}-[0-9]{4}', message: '' },
            ]}
            hasFeedback
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            label=''
            name='clinicAddress'
            rules={[{ required: true, message: '' }]}
            hasFeedback
          >
            <Input.TextArea
              placeholder=''
              style={{ width: '100%' }}
            />
          </Form.Item>
          <Form.Item
            label=''
            name='clinicWeb'
            rules={[{ type: 'url', message: '' }]}
            hasFeedback
          >
            <Input placeholder='' style={{ width: '100%' }} />
          </Form.Item>
          <Form.Item
            label=''
            name='timing'
            rules={[{ required: true, message: '' }]}
            hasFeedback
          >
            <TimePicker.RangePicker
              format='HH:mm'
              style={{ width: '100%' }}
              placeholder={['', '']}
            />
          </Form.Item>
          <Form.Item label='' name='clinicImage'>
            <Upload type='file' maxCount={1} beforeUpload={() => false}>
              <Button></Button>
            </Upload>
          </Form.Item>
          <Form.Item>
            <Button type='primary' htmlType='submit' className='add-btn'>
              
            </Button>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
}

export default ClinicForm;
