import React from 'react';
import { Button, Form, Input } from 'antd';
import { Link, useNavigate } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';
import logo from '../images/heart-logo.png';

function Login() {
  const navigate = useNavigate();

  const onFinish = async (value) => {
    try {
      const response = await axios.post('/api/user/login', value);
      if (response.data.success) {
        toast.success(response.data.message);
        localStorage.setItem('token', response.data.data);
        navigate('/');
      } else {
        toast.error(response.data.message);
      }
    } catch (error) {
      toast.error('');
    }
  };
  return (
    <div className='auth'>
      <div className='auth-form'>
        <img className='img-login' src={logo} alt='logo' />
        <h3 className='title'></h3>
        <Form className='form' layout='vertical' onFinish={onFinish}>
          <Form.Item
            label={<label style={{ color: 'white' }}>Email</label>}
            name='email'
            rules={[
              {
                required: true,
                message: '',
              },
              { type: 'email', message: '' },
            ]}
            hasFeedback
          >
            <Input placeholder='' />
          </Form.Item>
          <Form.Item
            label={<label style={{ color: 'white' }}></label>}
            name='password'
            rules={[
              {
                required: true,
                message: '',
              },
              {
                min: 6,
                max: 12,
                message: ' 6-12 ',
              },
            ]}
            hasFeedback
          >
            <Input.Password placeholder='' />
          </Form.Item>
          <p style={{ color: 'white' }}>
            ?{'  '}
            <Link to='/register' className='accout'>
              
            </Link>
          </p>
          <Form.Item className='btn-login'>
            <Button type='primary' htmlType='submit'>
              
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
}

export default Login;
