import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Layout from '../../components/Layout';
import { Table, Tabs, Button } from 'antd';
import toast from 'react-hot-toast';
import moment from 'moment';

function ClinicsList() {
  const { TabPane } = Tabs;
  const [clinics, setClinics] = useState([]);

  const getClinics = async () => {
    try {
      const response = await axios.get('/api/admin/get-all-clinics', {
        headers: {
          Authorization: `Bearer ${localStorage.getItem('token')}`,
        },
      });
      if (response.data.data) {
        setClinics(response.data.data);
      } else {
        toast.error(response.data.message);
      }
    } catch (error) {
      toast.error(' ');
    }
  };

  const changeClinicStatus = async (record, status) => {
    try {
      const response = await axios.post(
        '/api/admin/change-status-to-clinic',
        { clinicId: record._id, status: status },
        {
          headers: {
            Authorization: `Bearer ${localStorage.getItem('token')}`,
          },
        }
      );
      if (response.data.data) {
        getClinics();
      } else {
        toast.error(response.data.message);
      }
    } catch (error) {
      toast.error(' ');
    }
  };

  useEffect(() => {
    getClinics();
  }, []);

  const columns = [
    {
      title: '',
      dataIndex: 'clinicName',
      key: 'clinicName',
    },
    {
      title: '',
      dataIndex: 'clinicTel',
      key: 'clinicTel',
    },
    {
      title: '',
      dataIndex: 'clinicAddress',
      key: 'clinicAddress',
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: (record, text) => moment(record.createdAt).format('DD/MM/YYYY'),
    },
    {
      title: '',
      key: 'status',
      dataIndex: 'status',
    },
    {
      title: '',
      key: 'manage',
      render: (text, record) => (
        <div className='manage-btn'>
          {record.status === '' && (
            <Button onClick={() => changeClinicStatus(record, '')}>
              
            </Button>
          )}
          {record.status === '' && (
            <Button
              danger
              onClick={() => changeClinicStatus(record, '')}
            >
              
            </Button>
          )}
        </div>
      ),
    },
  ];

  return (
    <Layout>
      <h2></h2>
      <Tabs>
        <TabPane>
          <Table
            columns={columns}
            dataSource={clinics}
            scroll={{
              x: 700,
            }}
            pagination={{
              pageSize: 5,
            }}
          />
        </TabPane>
      </Tabs>
    </Layout>
  );
}

export default ClinicsList;
